/* Стилі для маленьких екранів, наприклад, мобільних телефонів */
@media (max-width: 600px) {
    .user-reply,
    .site-reply {
      border-radius: 10px; /* Зменшуємо радіус кутів */
      padding: 8px 16px; /* Зменшуємо відступи */
    }
  
    .test-container {
      margin-top: 10px; /* Зменшуємо відступи */
      padding: 10px; /* Зменшуємо відступи */
    }
  }
  
  /* Стилі для середніх і великих екранів */
  @media (min-width: 601px) {
    /* Ніяких змін, залишаємо стилі як є */
  }
  
  /* Стилі для корневого елемента сторінки */
  .page-container {
      max-width: 1000px; /* Ширина сторінки в пікселях */
      margin: 0 auto; /* Центруємо сторінку по горизонталі */
      padding: 50px; /* Внутрішні відступи сторінки */
  }
  
  /* Стилі для відповіді користувача */
  .user-reply {
      background-color: #f0f0f0;
      border-radius: 20px;
      padding: 10px 20px;
      margin-bottom: 10px;
      font-family: 'Roboto', sans-serif;
  }
  
  /* Стилі для відповіді сайту */
  .site-reply {
      background-color: #f9f9f9;
      border-radius: 20px;
      padding: 10px 20px;
      margin-bottom: 10px;
      font-family: 'Roboto', sans-serif;
  }
  
  /* Стилі для заголовка */
  .title {
      font-family: 'Roboto', sans-serif;
  }
  
  /* Стилі для підзаголовка */
  .subtitle {
      font-family: 'Roboto', sans-serif;
  }
  
  /* Стилі для контейнера тесту */
  .test-container {
      margin-top: 20px;
      padding: 20px;
      border: 1px solid #ccc;
      border-radius: 5px;
  }
  
  /* Стилі для питання тесту */
  .test-question {
      font-weight: bold;
      margin-bottom: 10px;
  }
  
  /* Стилі для опцій тесту */
  .test-options {
      margin-bottom: 10px;
  }
  
  /* Стилі для кнопки відповіді */
  .test-submit-button {
      padding: 8px 16px;
      background-color: #007bff;
      color: #fff;
      border: none;
      border-radius: 5px;
      cursor: pointer;
  }
  
  /* Стилі для кнопки відповіді при наведенні */
  .test-submit-button:hover {
      background-color: #0056b3;
  }
  